.panier-container {
    position: relative;
    max-width: 900px; /* Limiter la largeur du conteneur pour éviter qu'il s'étale trop */
    margin: 40px auto; /* Centrer verticalement et horizontalement */
    margin-left: calc(140px + auto);
    padding: 25px;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
}

/* Titre principal */
.panier-container h2 {
    font-size: 26px;
    color: #222;
    text-align: center;
    margin-bottom: 25px;
    font-weight: bold;
    text-transform: uppercase;
}

/* Liste des articles */
.article-list {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

/* Article individuel */
.article-item {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 20px;
    border: 1px solid #e4e4e4;
    border-radius: 10px;
    background-color: #fff;
    transition: box-shadow 0.3s ease, transform 0.2s ease;
}

.article-item:hover {
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
    transform: translateY(-3px);
}

/* Titre et contenu des articles */
.article-item h3 {
    font-size: 20px;
    color: #333;
    margin: 0;
    padding-bottom: 5px;
    border-bottom: 1px solid #eaeaea;
}

.article-item p {
    font-size: 14px;
    font-weight: 400;
    color: #555;
    line-height: 1.6;
    margin: 0;
}

/* Prix unitaire et total TTC */
.price {
    font-size: 16px;
    font-weight: 600;
    color: #000;
}

.article-total {
    font-size: 16px;
    font-weight: 600;
    color: #222;
}

.article-total span {
    font-size: 14px;
    color: #777;
}

/* Catégories */
.category {
    font-size: 12px;
    color: #888;
    font-style: italic;
    margin-bottom: 10px;
}

/* Avertissement pour le stock */
.stock-warning {
    font-size: 14px;
    color: #d9534f;
    font-weight: bold;
}

/* Actions */
.actions {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 15px;
}

.actions button {
    padding: 10px 12px;
    font-size: 16px;
    border: none;
    border-radius: 8px;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    transition: transform 0.2s ease, opacity 0.3s ease;
    font-weight: bold;
}

.actions button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
    opacity: 0.6;
}

.actions button:hover:not(:disabled) {
    background-color: #388e3c;
    transform: scale(1.1);
}

.actions p {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
    font-family: 'Arial', sans-serif;
    color: #222;
}

/* Conteneur pour le total général */
.total-container {
    margin-top: 30px;
    text-align: right;
    font-weight: bold;
    color: #222;
    padding: 20px 0;
    border-top: 2px solid #ddd;
    font-size: 20px;
}

.total-container span {
    font-size: 14px;
    color: #888;
}

/* Boutons : vider et valider */
.panier-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding-top: 15px;
    border-top: 2px solid #f0f0f0;
}

.panier-actions button {
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    transition: transform 0.3s ease, background-color 0.3s ease;
    font-weight: bold;
    cursor: pointer;
}

#clear-cart {
    background-color: #f44336;
    color: white;
}

#clear-cart:hover {
    background-color: #d63c2e;
    transform: scale(1.05);
}

#validate-cart {
    background-color: #2196F3;
    color: white;
}

#validate-cart:hover {
    background-color: #1976D2;
    transform: scale(1.05);
}

/* Message panier vide */
.empty-message {
    text-align: center;
    font-size: 18px;
    color: #777;
    padding: 30px;
    background-color: #f2f2f2;
    border-radius: 8px;
    border: 1px solid #e0e0e0;
    margin-top: 20px;
}

.empty-message a {
    display: inline-block;
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
    color: #2196F3;
    text-decoration: none;
    transition: color 0.3s ease;
}

.empty-message a:hover {
    color: #1e88e5;
}

