        margin: 10px 5px; /* Réduction encore plus forte */
        padding: 12px;
    }

    .panier-container h2 {
        font-size: 20px;
    }

    .article-item {
        padding: 10px;
        gap: 8px;
    }

    .actions button {
        font-size: 10px;
        padding: 5px 7px;
    }

    .panier-actions button {
        padding: 6px;
        font-size: 11px;
    }

    .total-container {
        font-size: 13px;
    }
}

/* 320px - Très petit écran (iPhone SE, téléphones anciens) */
@media (max-width: 320px) {
    .panier-container {
        margin: 8px 3px; /* Très petites marges */
